
#pag-welcome {
  position: relative; 
  display: flex;
  text-align: center;
  align-items: center;                                                /* the 'body' of the page */
  justify-content: center;
  height: 100vh;
  background-color: #191a1a;
}


.hover-text {
  display: none;
  position: absolute;
  bottom: -40px;                                                      /* The text that appears beneath when hovering */
  left: 50%;
  transform: translateX(-50%);
  padding: 5px 10px;
  background-color: rgb(255, 102, 0);
  color: white;
  border-radius: 3px;
  white-space: nowrap;
  font-size: 14px;
  cursor : not-allowed;
  opacity : 0.7;
}


/* each of the 3 images is in a icon-container */

.icon-container {
  position: relative;
  display: inline-block;
  margin-right: 35px;
}


.icon-container:last-child {
  margin-right: 0;
}


.icon-container:hover .hover-text {
  display: block;
}


/* for the header's animation */

@keyframes fadeIn {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.text-container {
  display: inline-block;
}

.text-container span {
  display: inline-block;
  opacity: 0;
  transform: translateX(-20px);
  animation: fadeIn 0.5s forwards;
}


/* for the button animation */

@keyframes fadeIn2 {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.91;
  }
}
