
#body {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 100vh;                                                          
    background-color: #191a1a;
  }
  
  
  #midcontainerx {
    background-color: #242525;    
    padding: 20px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    width: 425px;
    height : 575px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


#midcontainerx form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
  
#midcontainerx input {
    width: 400px;                                                            
    height: 49px;                                                            
    border-radius: 10px;                                          /* the inputs of the form */
    border: none; 
    font-size: 20px;                                                         
    background-color: white;
    background-position: 10px 10px; 
    background-repeat: no-repeat;
    padding: 12px 20px;                                                     
    box-sizing: border-box; 
}
  
  
  
#midcontainerx input:focus {
    outline: none; 
    border: none; 
    box-shadow: none; 
}
  
  
#midcontainerx input::placeholder {
    color: rgba(0, 0, 0, 0.5); 
}
  
  
#midcontainerx input:focus::placeholder {
    color: rgba(0, 0, 0, 0.3); 
}
  
#send {
    cursor: pointer;
}
  

 
